<template>
  <div style="display: flex;flex-direction: column;align-items: center;justify-content: center;">
    <button class="sukTest" @click="fetchData"> </button>
    <div id="realgrid" style="width:46%;height: 400px; margin-top:30px;"></div>
  </div>
</template>

<script>
import { ValueType } from 'realgrid';
import { GridView, LocalDataProvider } from 'realgrid';
import axios from 'axios';

export default {
  name: 'RealGrid',
  data() {
    return {
      dataProvider: null,
      gridView: null,
      rows: []  // rows    
    };
  },
  methods: {
    async fetchData() {
      try {
        const apiUrl = 'http://localhost:7001/searchTest';

        //   
        const response = await axios.get(apiUrl);
        const transformedData = response.data.map(item => ({
          EMAIL: item.email,
          INP_USR: item.inp_USR,
          NICKNAME: item.nickname,
          INPDATE: item.inpdate
        }));
        console.log(response)
        // RealGrid  
        this.dataProvider.setRows(transformedData);
        // this.dataProvider.setRows(this.rows);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },
    
    toggleNavbarElement(show) {
      const navbarElement = document.querySelector('.navbar');
      if (navbarElement) {
        navbarElement.style.display = show ? 'block' : 'none';
      }
    },
  },
  mounted() {
    //     
    this.toggleNavbarElement(false); // .navbar  

    const fields = [
      {fieldName: "EMAIL",    dataType: ValueType.TEXT},
      {fieldName: "INP_USR",  dataType: ValueType.TEXT},
      {fieldName: "NICKNAME", dataType: ValueType.TEXT},
      {fieldName: "INPDATE",  dataType: ValueType.DATE}
    ];

    const columns = [
      {name: "EMAIL",     fieldName: "EMAIL",   width: "300",  header: {text: ""}},
      {name: "INP_USR",   fieldName: "INP_USR", width: "60",   header: {text: ""}},
      {name: "NICKNAME",  fieldName: "NICKNAME",width: "120",  header: {text: ""}},
      {name: "INPDATE",   fieldName: "INPDATE", width: "120",  header: {text: ""}}
    ];

    this.dataProvider = new LocalDataProvider(false);
    this.gridView = new GridView('realgrid');
    this.gridView.setDataSource(this.dataProvider);
    this.dataProvider.setFields(fields);

    // columns  
    this.gridView.setColumns(columns);

    //  
    this.fetchData();
  },
  beforeUnmount() {
    //     
    this.toggleNavbarElement(true); // .navbar  
  },
};
</script>

<style scoped>
@import '@/css/realgrid-style.css';

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  width: 100%;
  margin-bottom: 15px;
}

.sukTest {
  margin-top: 100px;
  cursor: pointer;
  padding: 5px 10px;
  border: 1px solid black;
  border-radius: 5px;
  background-color: black;
  color: white;
  margin-left: 10px;
  margin-top: 20px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

</style>
