<template>
  <div class="materialContainer2 ui">
    <div class="box">
      <!--   -->
      <button class="close-button" @click="closeForm">
        <font-awesome-icon :icon="['fas', 'times']" />
      </button>

      <!-- Form -->
      <form id="Writeform" style="max-width:100%;">
        <div class="title2">Write</div>
        <div class="box-content">
          <!--   -->
          <div class="left-box">
            <!--  -->
            <div class="input" :class="{ 'focused': inputFocused === 'title', 'blured' : inputBlured === 'title' }">
              <label for="title" :style="input.titleStyle">Title</label>
              <input
                type="text"
                name="title"
                id="title"
                v-model="input.title"
                @focus="handleInputFocus('title')"
                @blur="handleInputBlur('title')"
              />
              <span class="spin"></span>
            </div>
            <div class="input">
              <div class="label-icon-container">
                <label for="nickname" style="line-height:18px;font-size:18px;">
                  <svg class="svg-inline--fa fa-user iconCSS" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="user" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path fill="currentColor" d="M224 256A128 128 0 1 0 224 0a128 128 0 1 0 0 256zm-45.7 48C79.8 304 0 383.8 0 482.3C0 498.7 13.3 512 29.7 512H418.3c16.4 0 29.7-13.3 29.7-29.7C448 383.8 368.2 304 269.7 304H178.3z"></path></svg>
                </label><input
                  type="text"
                  name="nickname"
                  id="nickname"
                  v-model="nickname"
                  />
                
                <span class="spin"></span>
              </div>
            </div>
          <div class="input-group">
    <!--     -->
    <div class="input" :class="{ 'focused': inputFocused === 'tag1', 'blured' : inputBlured === 'tag1' }">
      <label for="tag1" :style="input.tag1Style">Hashtag</label>
      <input
        type="text"
        name="tag1"
        id="tag1"
        v-model="input.tag1"
        @input="handleTagInput('tag1')"
        @focus="handleInputFocus('tag1')"
        @blur="handleInputBlur('tag1')"
        style="font-size: 16px; background: #f0f0f0; font-style: italic;"
      />
      <span class="spin"></span>
    </div>

    <!--     -->
    <div class="input" :class="{ 'focused': inputFocused === 'tag2', 'blured' : inputBlured === 'tag2' }">
      <input
        type="text"
        name="tag2"
        id="tag2"
        v-model="input.tag2"
        @input="handleTagInput('tag2')"
        @focus="handleInputFocus('tag2')"
        @blur="handleInputBlur('tag2')"
        style="font-size: 16px; background: #f0f0f0; font-style: italic;"
      />
      <span class="spin"></span>
    </div>

    <!--     -->
    <div class="input" :class="{ 'focused': inputFocused === 'tag3', 'blured' : inputBlured === 'tag3' }">
      <input
        type="text"
        name="tag3"
        id="tag3"
        v-model="input.tag3"
        @input="handleTagInput('tag3')"
        @focus="handleInputFocus('tag3')"
        @blur="handleInputBlur('tag3')"
        style="font-size: 16px; background: #f0f0f0; font-style: italic;"
      />
      <span class="spin"></span>
    </div>
  </div>
  </div>
          <!--   -->
          <div class="right-box">
            <!--  -->
            <div>
              <textarea name="cont" id="cont" v-model="input.cont" style="height:293px; background-color: lightgoldenrodyelloway" placeholder="Bitte geben Sie Ihren Inhalt ein."></textarea>
            </div>
          </div>
        </div>

        <!-- GO  -->
        <div class="button submit">
          <button type="submit" @click="register">
            <span>Regist</span>
            <i class="fa fa-check"></i>
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { v4 as uuidv4 } from 'uuid';

  export default {
    name: 'CommunityWrite',
    directives: {
  },
    data() {
      return {
        inputFocused: null,
        inputBlured: null,
        showCheckIcon: false,
        selectedFile: null,
        input: {
          nickname: '',  
          tag1: '#',
          tag2: '#',
          tag3: '#',
          title: '',
          cont:'',
          tag1Style:{'line-height': '18px',
            'font-size': '18px'},
          titleStyle:{},
          contStyle:{},
        },
        nickname: '',
      };
    },
    components: {
  
    },
    mounted() {
      var self = this;
      console.log(sessionStorage.getItem('userEmail'));
      $.ajax({
        url: 'http://localhost:7001/communityNickname',
        method: 'GET',
        dataType: 'json',
        data: {email: sessionStorage.getItem('userEmail')},
        success: function(data) {
          //  Vue  
          self.communityData = data;
          //  data  
          self.nickname = data[0].nickname;
          //   input  
          var nicknameInput = document.getElementById('nickname');
          if (nicknameInput) {
              nicknameInput.value = data[0].nickname;
              nicknameInput.setAttribute('readonly', true);
          } else {
              console.error('Nickname input element not found.');
          }
          console.log(data[0].nickname);
        },
        error: (error) => {
          console.error('Error fetching data:', error);
        },
      });
    },
    methods: {
      createPost() {
        console.log('Creating post:', this.post);
      },
      closeForm() {
        this.$emit("closeForm");
      },
      toggleTextarea() {
        if (this.inputFocused !== 'cont') {
            this.handleInputFocus('cont');
        }
      },
      handleTextareaFocus() {
        // textarea    .spin 
        this.$refs.contTextarea.nextSibling.style.display = 'none';
      },
      //   
      handleTagInput(tagName) {
      //    #   # 
      if (!this.input[tagName].startsWith('#')) {
        this.input[tagName] = '#' + this.input[tagName];
      }
    },
      handleInputFocus(val) {
        this.inputFocused = val;
        this.inputBlured = null;
      },
      handleInputBlur(val) {
        this.inputFocused = null;
        this.inputBlured = val;

        //    
        if (this.input.title !== '') {
            this.input.titleStyle = {
            'line-height': '18px',
            'font-size': '18px',
            };
        } else {
            this.input.titleStyle = {};
        }

        //    
        if (this.input.cont !== '') {
            this.input.contStyle = {
            'line-height': '18px',
            'font-size': '18px',
            };
        } else {
            this.input.contStyle = {};
        }
      },
      register() {
        //     

        event.preventDefault();

        const postData = {
          nickname: this.input.nickname,
          tag1: this.input.tag1,
          tag2: this.input.tag2,
          tag3: this.input.tag3,
          title: this.input.title,
          cont: this.input.cont,
          id:""
        };
        // UUID  ( ID)
        const uniqueId = uuidv4();
        postData.id = uniqueId;  //  UUID postData 

        console.log('Registering post:', postData);

        if(postData.title === ''){
          alert(" .");
          $("#title").focus();
          return;
        }
        if(postData.cont === ''){
          alert(" .");
          $("#cont").focus();
          return;
        }
        if(postData.tag1 === '' || postData.tag2 === '' || postData.tag3 === ''){
          alert(" .");
          $("#cont").focus();
          return;
        }
        if(postData.id === ''){
          alert("!  !");
          return;
        }

        $.ajax({
          type: "POST",
          url: "http://localhost:7001/insert",
          data: JSON.stringify(postData),
          contentType: 'application/json',
          success: function() {
            alert("  .");
            //      .
            // this.$emit("closeForm");
            const closeButton = document.querySelector('.close-button');
            const clickEvent = new Event('click');
            closeButton.dispatchEvent(clickEvent);
          },
          error: function(xhr, status, error) {
            alert("!  ! " + error);
          }
        });
      },
    },
  };
  
  
  </script>

<style scoped>
/* Writeform    */
#Writeform {
  max-width: 100%; /*   */
  margin: 20px auto -20px;
}

.materialContainer2 {
  width: 100%;
  max-width: 1111px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -59%);
  display: flex;
}

.box {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 9999px rgba(0, 0, 0, 100);
}

.ui {
  z-index: 100;
}

.box-content {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.left-box,
.right-box {
  flex: 1;
  padding: 20px;
}

.title2 {
  font-size: 34px;
  font-weight: 700;
  letter-spacing: 2px;
  color: #ED2553;
  margin-bottom: 20px;
  text-align: center;
}
.button.submit {
    width: 10%;
    left: 45%;
}
.focused label {
  line-height: 18px;
  font-size: 18px;
  /* font-weight: 100; */
  /* top: 0px; */
}

.focused .spin {
  width: 100%;
}
.blured label {
  line-height: 60px;
  font-size: 24px;
  font-weight: 300;
  top: 10px;
}

.blured .spin {
  width: 0%;
}
.input-group {
  display: flex;
}
.input {
  flex: 1;
  margin-right: 10px;  /*       */
}
</style>
